const translations = {
  good: "",
  "good-evil-neutral": "",
  evil: "",
  law: "",
  "law-chaos-neutral": "",
  chaos: "",

  "result:": ":",

  "lawful-good": "",
  "neutral-good": "",
  "chaotic-good": "",
  "lawful-neutral": "",
  "neutral-neutral": "",
  "chaotic-neutral": "",
  "lawful-evil": "",
  "neutral-evil": "",
  "chaotic-evil": "",

  "go back": "",
  "go to result": "",
  clear: "",
};

type TranslationKey = keyof typeof translations;
export const gt = (key: TranslationKey | string): string =>
  translations.hasOwnProperty(key) ? translations[key as TranslationKey] : key;
